/* Basic reset */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

/* Typography */
body {
  font-family: Arial, sans-serif;
  font-size: 16px;
  line-height: 1.5;
}

h1, h2, h3 {
  font-weight: bold;
  margin: 1rem 0;
}

h1 {
  font-size: 2rem;
}

h2 {
  font-size: 1.5rem;
}

h3 {
  font-size: 1.25rem;
}

p {
  margin: 1rem 0;
}

ul {
  list-style: none;
  margin: 1rem 0;
  padding: 0;
}

li {
  margin-bottom: 0.5rem;
}

/* Layout */
header {
  background-color: #333;
  color: #fff;
  padding: 1rem;
}

nav ul {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

nav ul li a {
  color: #fff;
  text-decoration: none;
  padding: 0.5rem;
  border-radius: 0.25rem;
}

nav ul li a:hover {
  background-color: #fff;
  color: #333;
}

main {
  max-width: 800px;
  margin: 0 auto;
  padding: 1rem;
}

section:not(:last-child) {
  margin-bottom: 2rem;
}

h2 {
  border-bottom: 1px solid #ccc;
  padding-bottom: 0.5rem;
}

/* Forms */
form label {
  display: block;
  margin-bottom: 0.5rem;
}

form input,
form textarea {
  width: 100%;
  padding: 0.5rem;
  margin-bottom: 1rem;
  border: 1px solid #ccc;
  border-radius: 0.25rem;
  resize: none;
}

form input[type="submit"] {
  background-color: #333;
  color: #fff;
  border: none;
  border-radius: 0.25rem;
  padding: 0.5rem;
  cursor: pointer;
}

form input[type="submit"]:hover {
  background-color: #fff;
  color: #333;
}

/* Footer */
footer {
  background-color: #333;
  color: #fff;
  text-align: center;
  padding: 1rem;
}

img {
  max-width: 500px;
  margin: 0 auto;
  display: block;
}
.image-wrapper {
  text-align: center;
}
img.profile {
  width: 200px;
  border-radius: 50%;
}
.skills {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.skill {
  width: 30%;
  margin-bottom: 20px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  text-align: center;
  font-weight: bold;
}

.skills-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.contact-links {
  display: inline-block;
  margin: 0 10px;
}

.contact-links a {
  color: #fff;
  text-decoration: none;
  padding: 5px 10px;
  font-size: 16px;
  font-weight: bold;
  border-radius: 5px;
  background-color: #333;
}

.contact-links a:hover {
  background-color: #555;
}

